.display {
	height: 50px;
	width: 150px;
	align-items: center;
	border-radius: 10%;
	text-align: center;
	justify-items: center;
	background: #c9d6ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#e2e2e2),
		to(#c9d6ff)
	);
	background: linear-gradient(
		to right,
		#e2e2e2,
		#c9d6ff
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.drum-machine-panel {
	margin: 10%;
	height: 500px;
	background: #bdc3c7; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#2c3e50),
		to(#bdc3c7)
	);
	background: linear-gradient(
		to right,
		#2c3e50,
		#bdc3c7
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	border-radius: 10%;
}
.drum-machine {
	display: inline-block;
	margin-left: 35%;
}

.drum-pad {
	height: 100px;
	width: 100px;
	color: teal;
	background-color: aquamarine;
	border-radius: 30%;
	background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#2c5364),
		color-stop(#203a43),
		to(#0f2027)
	);
	background: linear-gradient(
		to right,
		#2c5364,
		#203a43,
		#0f2027
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.drum-pads {
	display: grid;
	grid-template-columns: 100px 100px 100px;
	grid-column-gap: 5px;
	grid-row-gap: 5px;
	font-family: "Open Sans", "Helvetica Neue", sans-serif;
}

